<template>
  <div class="WP">
    <div v-if="firstLogin">
      <v-touch
        v-if="firstLogin"
        v-on:swipeleft="onSwipeLeft(page)"
      >
        <!-- <keep-alive> -->
        <router-view></router-view>
        <!-- </keep-alive> -->
      </v-touch>

      <div class="dots">
        <i v-for="(tab, index) in tabs" :key="index">
          <div v-if="page === index+1 && tabs[page-1].type==='slide' " class="dot activeDot"></div>
          <div v-else-if="tab.type==='slide' && tabs[page-1].type ==='slide' " class="dot"></div>
          <div v-else-if="tab.type==='enter' && page === index+1 &&tabs[page-1].type ==='enter'">
            <button round @click="toIndex">
              {{enterMes}}

              <!--{{GLOBAL.userData.language == 'cn'?'':'Try now'}}-->
              <!--{{this.GLOBAL.userData.language == 'cn'?'1':'Try now'}}-->
              <!--1-->
            </button>
          </div>
        </i>
      </div>
    </div>

    <div v-else>
      <router-view></router-view>
      <div class="dots">
        <button round @click="toIndex">
          {{enterMes}}


          <!--{{GLOBAL.userData.language == 'cn'?'':'Try now'}}-->
          <!--{{this.GLOBAL.userData.language == 'cn'?'1':'Try now'}}-->
          <!--1-->
        </button>
      </div>
    </div>
  </div>
</template>


<script>
  import Watermark from './watermark'

  export default {
    name: "WP",
    data() {
      return {
        enterMes: '',
        page: 1,
        html: '',
        firstLogin: true,
        showButton: false,
        tabs: [
          {
            name: "welcome1",
            path: "/1",
            type: "slide"
          },
          {
            name: "welcome2",
            path: "/2",
            type: "slide"
          },
          {
            name: "welcome3",
            path: "/3",
            type: "enter"
          }
        ]
      };
    },
    updated() {
      let name = this.$router.history.current.name;
      console.log();
      if (name === "welcome3") {
        this.page = 3;
      } else if (name === "welcome2") {
        this.page = 2;
      } else {
        this.page = 1;
      }
    },
    created() {


      // if (!this.GLOBAL.userData.wwid) {
      //   alert('wwid')
      //   window.opener = null;
      //   window.open("about:blank", "_top").close()
      // }


      var url = 'http://janssen.vinfol.com/adapter/janssen/v2/enterCount'
      url += `?userId=${this.GLOBAL.userData.wwid}`

      this.$http
        .get(url)
        .then(
          response => {
            console.log(response);
            this.GLOBAL.userData.role = response.body.role
            console.log(this.GLOBAL.userData.role);
          })


    },

    mounted() {
      var v = this
      if (window.location.href.includes('ruyi.ai')) {
        var url = window.location.href
        var code = url.split('code=')[1].split('&state=')[0]

        var msgUrl = "http://janssen.vinfol.com/adapter/janssen/v2/wx/info?code="
        msgUrl += code

        this.$http.get(msgUrl)
          .then(res => {
            // alert(JSON.stringify(res.body))
            // alert(res.body.UserId)
            var wwid = res.body.UserId
            this.wwid = res.body.UserId

            v.login(wwid)

            var welcomeApi = 'http://janssen.vinfol.com//adapter/janssen/v2/welcome?userId='
            welcomeApi += wwid

            this.$http.get(welcomeApi)
              .then(res => {
                // alert(JSON.stringify(res))
                var userName = res.body.userName
                // alert(userName)

                if (!userName) {
                  userName = ''
                }

                userName += wwid

                Watermark.set(userName)
              })

          })
      }


      let num = localStorage.getItem(`jassen${this.GLOBAL.userData.wwid}login`);
      if (num === null) {
        this.firstLogin = true;
      } else {
        this.firstLogin = false;
      }
      this.$router.push({path: `/welcomePage/${this.page}`});
    },
    methods: {
      login(wwid) {
        if (!wwid) {
          if (window.location.href.includes('ruyi.ai')){
            if(!this.GLOBAL.userData.wwid){
              alert('wwid')

              window.opener = null;
              window.open("about:blank", "_top").close()
            }

          }
        }
        if (wwid.length === 0) {
          // wwid = 1;
        }
        this.GLOBAL.userData.wwid = wwid;

        // alert(navigator.userAgent)

        this.GLOBAL.userData.language = 'cn'

        if (navigator.userAgent.indexOf('Language/zh_CN') > -1) {
          // alert('cn')
          this.GLOBAL.userData.language = 'cn'
          this.enterMes = ''
        }
        if (navigator.userAgent.indexOf('Language/en') > -1) {
          // alert('en')
          this.GLOBAL.userData.language = 'en'
          this.enterMes = 'Try now'

        }


        var u = navigator.userAgent, app = navigator.appVersion;
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //androiduc
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
        if (isAndroid) {
          // alert('isAndroid')
          this.GLOBAL.userData.deviceType = 'Android'
        }
        if (isiOS) {
          // alert('isiOS')
          this.GLOBAL.userData.deviceType = 'iOS'
        }

        // this.$router.replace({
        //   path: "/welcomePage"
        // });

      },


      onSwipeLeft: function () {
        if (this.page < this.tabs.length) {
          this.page++;
          this.$router.push({path: `/welcomePage/${this.page}`});
        }
      },
      onSwipeRight: function () {
        if (this.page > 1) {
          this.page--;
          this.$router.push({path: `/welcomePage/${this.page}`});
        }
      },
      toIndex() {
        this.$router.push({path: `/index`});
      }
    }
  };
</script>


<style lang="scss" scoped>
  .dots {
    position: fixed;
    bottom: calc((116 / 300) * (300 / 750) * 100vw);
    width: 100vw;
    text-align: center;
  }

  .dots1 {
    position: fixed;
    bottom: 10vh;
    width: 100vw;
    text-align: center;
  }

  .dot {
    position: relative;
    top: 3vw;
    display: inline-block;
    background-color: #c3c2c1;
    width: 3vw;
    height: 3vw;
    border-radius: 50%;
    margin-right: 5vw;
  }

  .activeDot {
    background-color: orange;
  }

  .WP button {
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(35, 100, 253, 1);
    font-size: $font-size-18px;
    background: rgba(255, 255, 255, 1);
    height: calc((80 / 300) * (300 / 740) * 100vw);
    box-shadow: 0px 4px 5.5px 4px rgba(13, 115, 239, 1);
    border-radius: 40px;
    border: 3px solid rgba(255, 255, 255, 1);
    width: calc((300 / 740) * 100vw);
  }
</style>



